import { Button } from "react-bootstrap";
import "./News.css";

const News = ({ img, title, msg, remove, id }) => {
  return (
    <div className='news_wrapper'>        
        <img src={img} alt={title} />
      <div className='news_wrapper_title'>
        <h6>{title}</h6>
        <p>{msg}</p>
      </div>    
        <Button variant='danger' size='sm' onClick={() => remove(id)}>
          Delete
        </Button>
    </div>
  );
};

export default News;
